        <div class="input__group">
          <div class="input__field">
            <label for="first_name">Ime</label>
            <input
              type="text"
              name="first_name"
              placeholder="ime"
              required
              id="first_name"
              class="login_input user_input"
              v-model="clientInput.first_name"
            />
          </div>

          <div class="input__field">
            <label for="last_name">Prezime</label>
            <input
              type="text"
              id="last_name"
              placeholder="prezime"
              required
              class="login_input user_input"
              v-model="clientInput.last_name"
            />
          </div>
        </div>

        <div class="input__group">
          <div class="input__field">
            <label for="email">E-mail</label>
            <input
              type="email"
              id="email"
              placeholder="e-mail"
              required
              class="login_input user_input"
              v-model="clientInput.email"
            />
          </div>

          <div class="input__field">
            <label for="mobile">Mobilni</label>
