          </div>
        </div>

        <div class="modify_btns">
          <action-buttons toForm="clients" />
          <delete-button
            @clicked="delClient()"
            v-if="!clientAttendances.length && !clientPayments.length"
          />

          <tooltip
            class="client__tooltip"
            tip="Klijent NE smije imati evidencija ili plaćanja da bi ga mogla obrisati."
          />
        </div>
      </form>
      <Camera v-else @cam-closed="handleCam" />
    </div>
    <!-- </transition> -->
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import Loading from "@/components/utils/Loading.vue";
import ActionButtons from "@/components/utils/ActionButtons.vue";
import DeleteButton from "@/components/utils/DeleteButton.vue";
import Tooltip from "@/components/utils/Tooltip.vue";
import CheckboxCustom from "@/components/utils/CheckboxCustom.vue";
import Camera from "@/components/Camera.vue";
import actionsNotify from "@/mixins/actionsNotify";
import mapSchedules from "@/mixins/mapSchedules";
import Datepicker from "vuejs-datepicker";
import { sr } from "vuejs-datepicker/dist/locale";

export default {
  name: "Client",

  components: {
    Loading,
    Datepicker,
    ActionButtons,
    DeleteButton,
    Tooltip,
    CheckboxCustom,
    Camera,
  },

  mixins: [actionsNotify, mapSchedules],

  data() {
    return {
      enterClient: false,
      sr: sr,
      schedule: "",
      clientAttendances: [],
      clientPayments: [],
      clientInput: {
        //_id: '',
        first_name: "",
        last_name: "",
        email: "",
        phone: "",
        mobile: "",
        address: "",
        date_started: new Date(),
        notes: "",
        picture: "",
        active: "",
      },
      appeared: false,
      isCamera: false,
    };
  },

  computed: {
    ...mapGetters([
      "getAllClients",
      "getOneClient",
      "getAllSchedules",
      "getAllAttendances",
      "getAllPayments",
      "getErrors",
      "loadingState",
    ]),
  },

  methods: {
    ...mapActions([
      "clientAdd",
      "clientUpdate",
      "clientDelete",
      "fetchSchedules",
      "fetchAttendances",
      "fetchPayments",
      "fetchClients",
      "fetchFromForm",
      "clearErrors",
      "setLoadingState",
    ]),

    handleCam() {
      this.isCamera = false;
    },

    async addClient() {
      this.setLoadingState(true);
      if (this.getOneClient._id) {
        await this.clientUpdate(this.clientInput);
        await this.fetchClients();
      } else {
        await this.clientAdd(this.clientInput);
      }
      this.setLoadingState(false);
      if (this.getErrors.length) {
        this.$toast.error(
          "Greška! " + this.getErrors,
          "OK",
          this.notificationSystem.options.error
        );
        this.clearErrors();
      } else {
        this.$toast.success(
          "Uspješno sačuvano!",
          "OK",
          this.notificationSystem.options.success
        );
        this.$router.push("/clients");
      }
    },

    async delEx() {
      await this.clientDelete(this.getOneClient);
      this.$toast.success(
        "Uspješno obrisano!",
        "OK",
        this.notificationSystem.options.success
      );
      this.$router.push("/clients");
    },

    mapAttendances() {
      let obj,
        arr = [];
      for (let i = 0; i < this.getAllAttendances.length; i++) {
        for (let j = 0; j < this.getAllAttendances[i].members.length; j++) {
          obj = {
            client: this.getAllAttendances[i].members[j].client,
          };
          arr.push(obj);
        }
      }
      return arr;
    },

    mapPayments() {
      let obj,
        arr = [];
      for (let i = 0; i < this.getAllPayments.length; i++) {
        for (let j = 0; j < this.getAllPayments[i].members.length; j++) {
          obj = {
            client: this.getAllPayments[i].members[j].client,
          };
          arr.push(obj);
        }
      }
      return arr;
    },

    /*     mapSchedules() {
      let obj,
        arr = [];
      for (let i = 0; i < this.getAllSchedules.length; i++) {
        for (let j = 0; j < this.getAllSchedules[i].members.length; j++) {
          obj = {
            title: this.getAllSchedules[i].title,
            startTime: this.getAllSchedules[i].startTime,
            client: this.getAllSchedules[i].members[j].client,
          };
          arr.push(obj);
        }
      }
      return arr;
    }, */

    async newClient() {
      this.setLoadingState(true);
      this.fetchFromForm("client");
      //await this.clientClear();
      this.$router.push("/clientactivity");
    },

    onAppeared() {
      this.appeared = true;
    },
  },

  async mounted() {
    if (this.getOneClient) {
      this.clientInput = this.getOneClient;
      if (!this.getOneClient.date_started) {
        this.clientInput.date_started = new Date();
      }
      if (!this.getAllSchedules.length) await this.fetchSchedules();
      this.schedule = this.mapSchedules().filter((post) => {
        return post.client._id == this.getOneClient._id;
      });
      if (!this.getAllAttendances.length) await this.fetchAttendances();
      this.clientAttendances = this.mapAttendances().filter((post) => {
        return post.client._id == this.getOneClient._id;
      });
      if (!this.getAllPayments.length) await this.fetchPayments();
      this.clientPayments = this.mapPayments().filter((post) => {
        return post.client._id == this.getOneClient._id;
      });
    }
    this.setLoadingState(false);
  },
};
</script>

<style>
.client__img_wrapper {
  display: flex;
  align-items: center;
  margin: 1em 0;
  gap: 1em;
}

.client__img {
  width: 100px;
  height: 70px;
  border-radius: 50%;
}

.client__active {
  align-self: center;
}
</style>
